import { LOCALE_ID, Inject, Injectable } from '@angular/core';
import { CalendarEventTitleFormatter, CalendarEvent } from 'angular-calendar';
import { DatePipe } from '@angular/common';
import { MyEvent } from '../models/my_event';

@Injectable()
export class CustomEventTitleFormatter extends CalendarEventTitleFormatter {

  constructor(@Inject(LOCALE_ID) private locale: string) {
    super();
  }

  month(event: MyEvent): string {
    return `${event.title}<br>
    <ul>
        <li>
        <b>Start:</b>
        ${new DatePipe(this.locale).transform(
          event.start,
        'MMM d, hh:mm a',
          this.locale )}
        </li>
        <li>
        <b>End:</b>
        ${new DatePipe(this.locale).transform(
          event.end,
      'MMM d, hh:mm a',
          this.locale )}
        </li>
        <li>
        <b>Location:</b>
        ${event.location}
        </li>
    </ul>`;
  }

  week(event: MyEvent): string {
    return `${event.title}<br>
    <ul>
        <li>
        <b>Start:</b>
        ${new DatePipe(this.locale).transform(
      event.start,
      'MMM d, hh:mm a',
      this.locale )}
        </li>
        <li>
        <b>End:</b>
        ${new DatePipe(this.locale).transform(
      event.end,
      'MMM d, hh:mm a',
      this.locale )}
        </li>
        <li>
        <b>Location:</b>
        ${event.location}
        </li>
    </ul>`;
  }

  day(event: MyEvent): string {
    return `${event.title}<br>
    <ul>
        <li>
        <b>Start:</b>
        ${new DatePipe(this.locale).transform(
      event.start,
      'MMM d, hh:mm a',
      this.locale )}
        </li>
        <li>
        <b>End:</b>
        ${new DatePipe(this.locale).transform(
      event.end,
      'MMM d, hh:mm a',
      this.locale )}
        </li>
        <li>
        <b>Location:</b>
        ${event.location}
        </li>
    </ul>`;
  }
}
