import { Component, OnInit } from '@angular/core';
import { isSameDay, isSameMonth } from 'date-fns';
import { CalendarEventTitleFormatter, CalendarView } from 'angular-calendar';
import { Router } from '@angular/router';
import { ActivityService } from 'src/app/core/services/activity.service';
import { Activity } from 'src/app/core/models/activity';
import { CustomEventTitleFormatter } from 'src/app/core/services/custom-event-formatter.service';
import { MyEvent } from 'src/app/core/models/my_event';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-activities-calendar',
  templateUrl: './activities-calendar.component.html',
  styleUrls: ['./activities-calendar.component.scss'],
  providers: [
    {
      provide: CalendarEventTitleFormatter,
      useClass: CustomEventTitleFormatter
    }
  ]
})

export class ActivitiesCalendarComponent implements OnInit {
  view: CalendarView = CalendarView.Month;

  CalendarView = CalendarView;

  viewDate: Date = new Date();

  activities: Activity[];
  events: MyEvent[] = [];

  activeDayIsOpen = true;

  refresh: Subject<any> = new Subject();

  constructor(private router: Router, private activityService: ActivityService) { }

  ngOnInit() {
    this.showActivities();
    this.dayClicked({date: this.viewDate, events: this.events});
  }

  dayClicked({ date, events }: { date: Date; events: MyEvent[] }): void {
    if (isSameMonth(date, this.viewDate)) {
      if ((isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) || events.length === 0) {
        this.activeDayIsOpen = false;
      } else {
        this.activeDayIsOpen = true;
      }
      this.viewDate = date;
    }
  }

  handleClick(event: MyEvent): void {
    this.router.navigate(['/activities'], {queryParams: {id: event.id.toString()}});
  }

  setView(view: CalendarView) {
    this.view = view;
  }

  closeOpenMonthViewDay() {
    this.activeDayIsOpen = false;
  }

  showActivities(): void {
    this.activityService.getActivities().subscribe(activities => {
      this.activities = activities;
      for (const activity of this.activities) {
        const event = {
          id: activity.id,
          title: activity.title,
          start: new Date(activity.start_date),
          end: new Date(activity.end_date),
          location: activity.location
        };
        this.events.push(event);
      }
    });
  }
}
