import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ActivitiesComponent } from './activities.component';
import { ActivitiesCalendarComponent } from './pages/activities-calendar/activities-calendar.component';
import { PhotoGalleriesComponent } from './pages/photo-galleries/photo-galleries.component';
import { AuthGuard } from '../../core/guards/auth.guard';
import { ActivityDetailComponent } from './pages/activity-detail/activity-detail.component';
import { ActivityResolver } from 'src/app/core/services/resolvers/activity.resolver';
import { UpcomingActivitiesComponent } from './pages/upcoming-activities/upcoming-activities.component';
import { MyRegistrationsComponent } from './pages/my-registrations/my-registrations.component';

const routes: Routes =
  [
    { 
      path: '', 
      component: ActivitiesComponent,
      children: [
        {
          path: '',
          component: UpcomingActivitiesComponent
        },
        { 
          path: 'activities_calendar', 
          component: ActivitiesCalendarComponent 
        },
        {
          path: 'my_registrations',
          component: MyRegistrationsComponent
        }
      ] 
    },
    { 
      path: 'photo_galleries', 
      component: PhotoGalleriesComponent, 
      canActivate: [AuthGuard] 
    },
    {
      path: ':id',
      component: ActivityDetailComponent,
      resolve: {
        activity: ActivityResolver 
      }
    },
  ];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ActivitiesRoutingModule { }
