import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Activity, ActivityRegistration } from 'src/app/core/models/activity';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { RegisterModalComponent } from 'src/app/shared/components/activity-helpers/register-modal/register-modal.component';
import { ActivityService } from 'src/app/core/services/activity.service';
import { AuthenticationService } from 'src/app/core/services/authentication.service';
import { MemberService } from 'src/app/core/services/member.service';
import { Member } from 'src/app/core/models/member';
import { DeleteModalComponent } from 'src/app/shared/components/delete-modal/delete-modal.component';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-activity-detail',
  templateUrl: './activity-detail.component.html',
  styleUrls: ['./activity-detail.component.scss']
})
export class ActivityDetailComponent implements OnInit {
  activity: Activity;
  member: Member;
  isRegistered: boolean = false;
  registrationClosed = false;
  _canRegister = true;
  registration: ActivityRegistration;

  constructor(
    private actr: ActivatedRoute,
    private modalService: NgbModal,
    private activityService: ActivityService,
    private authService: AuthenticationService,
    private sanitizer: DomSanitizer) { }

  ngOnInit() {
    
    this.actr.data.subscribe((data: any) => {
      this.activity = data.activity;
      if (this.canRegister(this.activity.registration_opens, this.activity.registration_closes)) {
        this._canRegister = true;
      }
      else {
        this._canRegister = false;
      }
      console.log(this.activity)
    });

    const member = this.authService._getUser();
    if(member) {
      this.member = member;
      this.getRegistrations();
    }
  }

  get description() {
    return this.sanitizer.bypassSecurityTrustHtml(this.activity.description);
  }

  canRegister(open_date,closing_date) {
    if(new Date(open_date).getTime() <= Date.now() && Date.now() < new Date(closing_date).getTime()) {
      return true;
    }
    return false;
  }

  getRegistrations() {
    this.activityService.getRegistrations(this.activity.id, this.member.id).subscribe(
      registrations => {
        console.log(registrations);
        this.registration = registrations[0];
        if(registrations.length > 0) {
          this.isRegistered = true;
        }
      },
      error => {
        console.log("error", error);
      }
    )
  } 

  onRegister() {
    const modalRef = this.modalService.open(RegisterModalComponent, { backdrop: 'static' });

    modalRef.componentInstance.activityId = this.activity.id;
    modalRef.componentInstance.showSearchbar = false;
    modalRef.componentInstance.selectedMember = this.member;
    modalRef.componentInstance.activityOptions = this.activity.options;

    modalRef.componentInstance.register.subscribe(data => {
      this.registerMember(data);
      modalRef.close();
    })
  }

  onDeRegister() {
    const modalRef = this.modalService.open(DeleteModalComponent);
    
    modalRef.componentInstance.action = "de-register";
    modalRef.componentInstance.objRef = "activity";

    modalRef.componentInstance.delete.subscribe(() => {
      this.deRegister();
    })
  }

  deRegister() {
    this.activityService.deleteRegistration(this.activity.id, this.registration.id).subscribe(
      response => {
        console.log("You have been de-registerd successfully", response);
        this.isRegistered = false;
      },
      error => {
        console.log("An error occurred while de-registering", error);
      }
    )
  }

  registerMember(data: any) {
    console.log(data);
    this.activityService.registerMember(data).subscribe(
      registration => {
        this.isRegistered = true;
        this.getRegistrations();
      },
      error => {
        console.log("An erro has occurred while registering", error);
      }
    )
  }
}
